.overlay {
  background-image: url("./media/banner.jpg")!important;
}

@media (min-width: 992px)
.container {
    max-width: 960px;
}
@media (min-width: 768px)
.container {
    max-width: 720px;
}
@media (min-width: 576px)
.container {
    max-width: 540px;
}
.container {
    width: 100%!important;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

.site-section-cover .container > .row {
  height: calc(120vh - 196px);
  min-height: 677px;
}

.site-section-cover {
  position: relative;
  background-size: cover;
  background-repeat: no-repeat;
}

.site-section {
  padding: 7rem 0;
}
@media (min-width: 768px)
.site-section {
  padding: 5em 0;
}
.site-section {
  padding: 2.5em 0;
}

@media (min-width: 768px)
.site-footer {
    padding: 8em 0;
}
.site-footer {
    padding: 4em 0;
    background: #efefef;
}

.site-section-cover.overlay:before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  z-index: 1;
}

.site-section-cover, .site-section-cover .container {
  position: relative;
  z-index: 2;
}

.navbar {
  margin-bottom: 0px;
  z-index: 1999;
  position: fixed!important;
  top: 0;
  height: 80px;
  width: 100%;
  padding: 1rem;
  background-color: #010013!important;
  
}

@media (max-width: 991.98px)
.navbar {
    padding-top: 3rem;
    padding-bottom: 3rem;
}

@media (max-width: 991.98px)
.navbar {
    float: left;
    position: relative;
}
.site-navbar .site-logo {
    margin: 0;
    padding: 0;
    font-size: 1.7rem;
}

.text-white {
  color: #fff!important;
}

